<template>
  <div ref="chart2" style="width: 80%; height: 376px"></div>
</template>

<script>
export default {
  data() {
    return {};
  },
  mounted() {
    this.getEchartData();
  },
  methods: {
    getEchartData() {
      var myChart2 = this.$echarts.init(this.$refs.chart2);
      myChart2.setOption({
        title: {
          text: "饼图",
          link: "https://www.baidu.com",
          textStyle: {
            color: "green",
          },
        },
        legend: {},
        series: [
          {
            type: "pie",
            data: [
                {
                    value:30,
                    name: "裤子"
                }, 
                {
                    value:40,
                    name: "衣服"
                }, 
                {
                    value:60,
                    name: "帽子"
                }, 
            ],
            label:{
                show: true,
                // formatter:'haha',//决定文字内容
                formatter: function (e) { 
                   return e.data.name+'的数量为'+e.data.value+'\n占比：'+e.percent+'%'
                }
            },
            // radius:20//饼图半径
            // radius:['50%','60%'],//圆环效果
            // roseType:'radius' //南丁格尔图 鸡肋
            //  selectedMode:'single' //点击偏离效果
          },
        ],
      });
    },
  },
};
</script>

